import React from "react";
import API from "../../utils/API";
import Container from "../../components/Container";
import SearchResults from "../../components/SearchResults";
import SearchForm from "../../components/SearchForm";
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import CardDeck from 'react-bootstrap/CardDeck';
import Card from 'react-bootstrap/Card';


  class SearchPage extends React.Component {
    constructor(props) {
      super(props);

      
      this.state = {
        labels: [],
        contracts: [],
        formValues:{},
        pdfs:[],
        titles:[],
        alertShow: false,
        show: false,
      };
      this.onClick = this.handleContract.bind(this);
      this.handleShow = this.handleShow.bind(this);
      this.handleClose = this.handleClose.bind(this);
      this.handleShowAlert = this.handleShowAlert.bind(this);
      this.handleCloseAlert = this.handleCloseAlert.bind(this)
    }
  
    initialState = { 
      formValues: {},
    }

    state = this.initialState

    componentDidMount(){
      this.loadLabels();
    }

    componentWillUnmount(){
    }
    toggleHidden () {
      this.setState({
        isHidden: !this.state.isHidden
      })
    }

    handleClose() {
      this.setState({ show: false });
    }
  
    handleShow() {
      this.setState({ show: true });
    }
    handleCloseAlert() {
      this.setState({ alertShow: false });
    }
  
    handleShowAlert() {
      this.setState({ alertShow: true });
    }
    loadLabels = () => {
      API.getLabels()
        .then(res => {
          const labels = res.data;
          console.log(labels)
            this.setState({ labels })
        })
        .catch(err => console.log(err));
    };
   
    handleInputChange = (e) => {
      console.log("==handleInputChange==")
      let newValues = JSON.parse(JSON.stringify(this.state.formValues))
      newValues[e.target.name] = e.target.value
      this.setState({
        formValues: newValues
      })
      console.log("newFormValues is: " + JSON.stringify(newValues))
    }
    clear = () => {
      let emptyValues = JSON.parse(JSON.stringify(this.state.formValues))
      console.log(emptyValues)
      Object.keys(emptyValues).forEach(key => emptyValues[key] = "")
      this.setState({
        formValues: {},
        contracts:[],
      }) 
      console.log(JSON.parse(JSON.stringify(this.state.formValues)))
      console.log(emptyValues)
    }


    handleContract = (id) => {
      API.openRow(id)
      .then(res => {
        const pdfs = res.data;
        this.setState({pdfs});
        // this.props.history.push({
        //   state: { labels:this.state.labels,
        //     pdfs:this.state.pdfs,
        //     titles:this.state.titles }
        // })
      })
      .catch(err => console.log(err));
      API.titles(id)
      .then(res => {
        const titles = res.data;
        this.setState({titles});
      })
      this.setState({ show: true });
    }
    loadContracts = (query) => {
      console.log("==loadContracts==")
    API.search(query)
    .then(res => {
      const contracts = res.data;
      if (contracts.length > 0 ){
         this.setState({ contracts });
         console.log(contracts)
      }
      else {
       this.noResults();
      }
      console.log("formValue is"+JSON.stringify(this.state.formValues));
    })
    };
    handleFormSubmit = event => {  
    event.preventDefault();
    const formData = this.state.formValues
    let query = '';
    let keys = Object.keys(formData);
    
    keys.forEach(k => { 
     if(formData[k]){
      if (query !== "")
      query += `&`;
      query += `filter=`
      query += `${k}|${formData[k]}`
     }
     })
     return this.loadContracts(query);
    };
    noResults() {
      
      this.setState({alertShow:true})
      this.setState({contracts:[]})
    }

    
render() {


  
return ( 
  
<div className="container" style={{ marginTop: "80px" }}>
  <div className="jumbotron">
    <div className="container">
      <h1>Contract Document Search</h1>
    </div>
<br/>
  <Container>

  <SearchForm 
    labels={this.state.labels}
    formValues={this.state.formValues}
    handleFormSubmit={this.handleFormSubmit}
    handleInputChange={this.handleInputChange}
    // handleReset={this.handleReset}
    handleFormReset={this.handleFormReset}
    clear={this.clear}
    clearField={this.clearField}
    />

    <div className='modal' data-toggle="modal">
          <Modal show={this.state.alertShow} 
          onHide={this.handleCloseAlert}
          {...this.props}
          data-toggle="modal"
          size="sm"
          aria-labelledby="contained-modal-title-vcenter"
          centered>
          <Modal.Header closeButton>
            <Modal.Body>No results found</Modal.Body>
          </Modal.Header>
        </Modal>
      </div> 

{/* 
        <Button variant="primary" data-target="#myModal" data-toggle="modal" onClick={this.handleShowAlert}>
          test
        </Button> */}


        <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        ...
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary">Save changes</button>
      </div>
    </div>
  </div>
</div>
</Container>
</div>


<div className="container">
 <div className="jumbotron-fluid">
 </div>
  <SearchResults 
    labels={this.state.labels}
    contracts={this.state.contracts} 
    pdfs={this.state.pdfs}
    handleContract={this.onClick}
    handleTitles={this.onClick}
    />
    <br/>
    <br/>

</div>
<div>
  <Container>

   <br/>

    <Modal show={this.state.show} onHide={this.handleClose} className="modal-size" centered size="xl">
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Contract Attachments
          </Modal.Title>
        </Modal.Header>
       <div className="container">

         <br/>
         <Modal.Body>
    <table className="table table-striped">
      <thead>
        <tr className="title-header">
        {this.state.titles.map(contract => contract.Fields.map(title => <th key={title.Id}>{title.DisplayName}</th>))}
        </tr>
      </thead>
      <tbody className="title-row">

      {this.state.titles.map(contract => contract.Fields.map(title => <th key={title.Id} className="contract-title">{title.DataValue}</th>))}

      </tbody>
    </table>
   <CardDeck>
    <div className="container"><br/>
  <div className="row">
    {this.state.pdfs.map(contract =>  
    <div className="col-sm-4" key={contract.Id}>
      <Card className="card attachment" style={{marginBottom:15}}>
                <iframe
                title="pdf"
                  src={"http://davidm16.mhccom.com:9006/api/document/" + contract.DocImage.sfScanIndex}
                  width="100%"
                  type="application/pdf"
                ></iframe>
            <Card.Header>
                <a href={"http://davidm16.mhccom.com:9006/api/document/" + contract.DocImage.sfScanIndex}
              className="link"
              target="_blank"
              rel="noopener noreferrer"
            ><span><i className="fas fa-file-pdf fa-lg"> </i> <span style={{fontSize:12}}>{contract.DocImage.sfFilename}</span></span></a>
            </Card.Header>
                  <Card.Body>
            <Card.Text>
              {contract.Fields.map(docs => 
            <div>
              <span className="card-title"><strong>{docs.DisplayName}: </strong></span>
              <span className="card-text">{docs.DataValue}</span>
            </div>)}
            </Card.Text>
        </Card.Body>
      </Card>
    </div>)}
  </div>
</div>
</CardDeck>
<br/>
</Modal.Body>
    </div>
    <Modal.Footer>
    <Button variant="primary" onClick={this.handleClose}>
              Close
            </Button>
            </Modal.Footer>
    </Modal>
     </Container>
</div> 

    <br/>
  </div>

      );
    }
  }




export default SearchPage;
