import React from "react";
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import CardDeck from 'react-bootstrap/CardDeck';
import Card from 'react-bootstrap/Card';


// const Pdfs = props => (
  class Pdfs extends React.Component {
    render() {
      return (
<div>
<Modal {...this.props}  className="modal-size" centered size="xl">
    <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Contract attachments
          </Modal.Title>
        </Modal.Header>
       <div className="container">
         {/* {console.log(props) } */}
         <br/>
         <Modal.Body>
    <table className="table table-striped">
      <thead>
        <tr className="title-header">
        {this.props.titles.map(contract => contract.Fields.map(title => <th key={title.Id}>{title.DisplayName}</th>))}
        </tr>
      </thead>
      <tbody className="title-row">

      {this.titles.map(contract => contract.Fields.map(title => <th key={title.Id} className="contract-title">{title.DataValue}</th>))}

      </tbody>
    </table>
   <CardDeck>
    <div className="container"><br/>
  <div className="row">
    {this.pdfs.map(contract =>  
    <div className="col-sm-4" key={contract.Id}>
      <Card className="card attachment" style={{marginBottom:15}}>
            <Card.Header>
                <a href={"http://davidm16.mhccom.com:9006/api/document/" + contract.DocImage.sfScanIndex}
              className="link"
              target="_blank"
              rel="noopener noreferrer"
            ><span><i className="fas fa-file-pdf fa-lg"> </i> <span style={{fontSize:12}}>{contract.DocImage.sfFilename}</span></span></a>
            </Card.Header>
                  <Card.Body>
            <Card.Text>
              {contract.Fields.map(docs => 
            <div>
              <span className="card-title"><strong>{docs.DisplayName}: </strong></span>
              <span className="card-text">{docs.DataValue}</span>
            </div>)}
            </Card.Text>
        </Card.Body>
      </Card>
    </div>)}
  </div>
</div>
</CardDeck>
<br/>
</Modal.Body>
    </div>
    <Modal.Footer>
    <Button variant="primary" onClick={this.handleClose}>
              Close
            </Button>
            </Modal.Footer>
    </Modal>
    </div>
    );
  }
}
export default Pdfs;

