import React, { Component } from "react"
import Button from 'react-bootstrap/Button';
import { Input, Labels } from "../Form/index";
import Help from "../../components/Help";

var styles = {
    width: "18rem"
  };


  export default class SearchForm extends Component {

    constructor(...args) {
      super(...args);
  
      this.state = { 
      };
    }

    render() {
      return ( 

<form className="form-inline col-md-12" onReset={this.props.handleFormReset}>
   
   {this.props.labels.map(label => (
     <div className="card border-0 mx-auto" style={styles} key={label.Id}>
          <ul className="list-inline ">
             <span>
               <li>
                 <Labels  htmlFor={label.DisplayName} >{label.DisplayName}:</Labels>
               </li>
               <li >
                 <div>
                  <Input  
                   key={label.Id}
                   onChange={this.props.handleInputChange}
                   value={this.props.formValues[label.DataField] ||""}
                   type="search"
                   maxLength="999"
                   style={{height:34}}
                   name={label.DataField ||""}
                   className={"form-control mb-2 mr-sm-2"} 
                   id={label.DataField}
                 />
                 </div>
               </li> 
             </span>
         </ul>
     </div>
   ))}

  <div className=" col-sm-12">

  <Button
        style={{ float: "left", marginBottom: 10 }} 
        className="btn btn-success"
        type="submit"
        onClick={this.props.handleFormSubmit}
      >
        Search
      </Button>

      <Help />

      <Button
        style={{ float: "left", marginBottom: 10 }} 
        className="btn btn-secondary"
        // type="reset"
        onClick={this.props.clear}
      >
        Reset
      </Button>


  </div>
  
  </form>
    );
  }
}

