import axios from "axios"; 

export default {
  getLabels: function() {
    return axios.get("http://davidm16.mhccom.com:9006/api/ixfieldsettings" );
  },
  search: function(query) {
    console.log("==API==")
    console.log("this is the query string " + query)
    console.log("********************")
    return axios.get("http://davidm16.mhccom.com:9006/api/ixmasterdocument?" + query );
  },
  titles: function(id) {
    console.log("this is the title string " + id)
    return axios.get("http://davidm16.mhccom.com:9006/api/ixmasterdocument?filter=IDXT001|" + id );
  },
  openRow: function(id) {
    console.log(id)
    return axios.get("http://davidm16.mhccom.com:9006/api/ixdocument/?masterDocId=" + id );
    
  }
  };


